fun generateCookie(name: String, value: String) {
    val date = Date()
    val ex = "expires=" + Date(date.getTime() + (24 * 60 * 60 * 1000)).toUTCString()
    document.cookie = name + "=" + value + ";" + ex + ";path=/"
}


fun accessCookie(name: String): String {
    val value = document.cookie.split(';').find {row -> row.startsWith(name)}
    return if(value != null) value.split('=')[1]
    else ""
}

object Constants {
    val mainRectRatio = 0.8 // width / height
    val rows = listOf(144 / 2, 125 / 2, 144 / 2, 400 / 2)
    val maxRows = listOf(144, 960, 125, 144, 400)
    val targetWidth = 600
    val targetHeight: Int
        get() {
            return maxRows.sum()
        }
    val heightPaddingToMain = 20
    val widthPaddingToMain = 20
    val dictionary = Dictionary()
    val letterLimit = 5
    val attempts = 6
}

fun bg(color: String) {
    if (color != "") {
        document.bgColor = color
    }
}

fun Node.sayHello(game: Game) {
    val topPart = TopPart()
    val keyboard = Keyboard()
    val midBanner = MidBanner()
    val adBanner = AdBanner()
    val gridPart = GridPart(
        Constants.targetWidth.toDouble(),
        window.innerHeight - 20.0 - Constants.rows.sum(),
        Constants.attempts,
        Constants.letterLimit
    )

    keyboard.letterPressed = { c ->
        if(!game.gameSolved){
            game.addLetter(c)
            gridPart.writeLetter(game.recordedWord)
        }

    }

    keyboard.erasePressed = {
        if(!game.gameSolved){
            game.removeLetter()
            gridPart.writeLetter(game.recordedWord)
        }
    }

    keyboard.enterPressed = {
        if(!game.gameSolved){
            val rec = game.recordedWord
            val res = game.checkWord()
            if (res != null){
                gridPart.paintRow(res)
                keyboard.paintKeys(res.first,res.second,rec)
            }
            if(game.gameSolved){
                window.alert("Cevap bulundu, yürü be.");
                // TODO game solved
            }
            gridPart.activeRow = game.activeRow
            gridPart.moveTriangle()
        }
    }

    topPart.replayFunction = {
        game.startRound()
        keyboard.resetKeys()
        gridPart.clear()
        gridPart.activeRow = game.activeRow
        gridPart.moveTriangle()
    }


    append {
        div {
            id = "main"
        }.also {
            it.style.let { style ->
                style.color = "#110A01"
                style.margin = "auto"
                style.width = toPx(Constants.targetWidth)
                style.height = toPx(window.innerHeight-20)
                style.backgroundColor = "#AA55FF"
                style.position = "relative"
            }

            it.append {
                div{
                    id="topPart"
                    style = """
                        margin: auto;
                         height: ${toPx(Constants.rows[0])};
                         background-color: #ECEAEF;
                         font-family: testFont;
                    """.trimIndent()
                }.also {
                    it.append(topPart.generateTopPart())
                }



                div{
                    id = "grid"
                    style="""
                        height: ${toPx(window.innerHeight-20 - Constants.rows.sum())};
                    """
                }.also {
                    it.append(gridPart.createGrid())
                }
                div{
                    id = "commercial"
                    style = """
                        margin: auto;
                        width: 100%;
                        height: ${toPx(Constants.rows[1])};
                        position: absolute;
                        bottom: ${toPx(Constants.rows[3]+Constants.rows[2])};
                        left: 0;
                        background-color: #44FF44;
                    """
                }.also {
                    it.append(adBanner.generateAdBanner())
                }


                div {
                    id = "midBannerBox"
                    style = """
                        width: 100%;
                        height: ${toPx(Constants.rows[2])};
                        position: absolute;
                        bottom: ${toPx(Constants.rows[3])};
                        left: 0px;
                    """
                }.also {
                    it.append(midBanner.generate(Constants.targetWidth,Constants.rows[2]))
                }




                div {
                    id = "keyboardBox"
                    style = """
                        width: 100%;
                        height: ${toPx(Constants.rows[3])};
                        position: absolute;
                        bottom: 0px;
                        left: 0px;                        
                    """
                }.also {
                    it.append(keyboard.generate())
                }
            }
        }
    }

    window.onresize = {
        resizeFunc()
    }
}







fun toPx(v: Double): String {



    return v.toInt().toString() + "px"
}

fun toPx(v: Int): String {
    return v.toString()+"px"
}


class InnerRect(width: Double, height: Double) {
    val innerRect = Rectangle(0.0, width, 0.0, height)

    constructor(width: Int, height: Int) : this(width.toDouble(), height.toDouble())

    val mainRect =
        formMainRect()// innerRect.resized( w = innerRect.height/innerRect.width*Constants.mainRectRatio )

    private fun formMainRect(): Rectangle {
        /*
        if(innerRect.height<Constants.targetHeight){ // accordeon case

        }else{ // brick case
            val a = innerRect.height*Constants.mainRectRatio
            val r =  if(a<=innerRect.width){
                innerRect.resized( w = innerRect.height/innerRect.width*Constants.mainRectRatio )
            }else{
                innerRect.resized( h = innerRect.width/innerRect.height/Constants.mainRectRatio )
            }
            return Rectangle(r.centre,r.width-Constants.widthPaddingToMain, r.height-Constants.heightPaddingToMain)
        }

         */



        val a = innerRect.height * Constants.mainRectRatio
        val r = if (a <= innerRect.width) {
            innerRect.resized(w = innerRect.height / innerRect.width * Constants.mainRectRatio)
        } else {
            innerRect.resized(h = innerRect.width / innerRect.height / Constants.mainRectRatio)
        }
        return Rectangle(r.centre, r.width - Constants.widthPaddingToMain, r.height - Constants.heightPaddingToMain)


    }
}